.problemslist-div-backgroud{
    padding: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url('/img/background.png');
}

.problemslist-div-main {
    width: 100%;
    max-width: 1100px;
    min-height: 300px;
    padding: 24px;    
    border-radius: 10px;
    background:#ffffff;
    -webkit-box-shadow: 0 0 1em  rgba(150, 150, 150, 0.47);
    -moz-box-shadow:    0 0 1em  rgba(150, 150, 150, 0.47);
    box-shadow:         0 0 1em  rgba(150, 150, 150, 0.47);
}

.problemslist-title{
    text-align: center;
    font-size: 32px;
    color: rgb(0,21,41);
}

.problemslist-tag{
    margin: 2px;
    -webkit-box-shadow: 1px 1px 1px rgba(150, 150, 150, 0.47);
    -moz-box-shadow:    1px 1px 1px rgba(150, 150, 150, 0.47);
    box-shadow:         1px 1px 1px rgba(150, 150, 150, 0.47);    
}

.problemslist-div-select{
    padding: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.problemslist-select{
    width: 100%;
    margin: 2px;
}
