.subject-div-item{
    width: 200px;   
}
.subject-div-center{
    margin: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.subject-icon{
    width: 100%;
    height: 120px;

    background: rgb(0,21,41);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 60px;
}

.subject-title{
    font-weight: bold;
    color: rgb(0,21,41);
    font-size: medium;
    text-align: center;    
}