.editcontent-div-backgroud{
   padding: 24px;
   background-image: url('/img/background.png');
   display: flex;
   align-items: center;
   justify-content: center;
}

.editcontent-div-main {
   width: 100%;
   max-width: 1100px;
   min-height: 80vh;
   padding: 24px;    
   border-radius: 10px;
   background:#ffffff;
   -webkit-box-shadow: 0 0 1em  rgba(150, 150, 150, 0.47);
   -moz-box-shadow:    0 0 1em  rgba(150, 150, 150, 0.47);
   box-shadow:         0 0 1em  rgba(150, 150, 150, 0.47);
}

.editcontent-title{
   text-align: center;
   font-size: 32px;
   color: rgb(0,21,41);
}

.editcontent-select{
   width: 100%;
}

.editcontent-div-mdeditor{
   height: 80vh;
}
.editcontent-div-button{
   padding: 10px;
   display: flex;
   align-items: center;
   justify-content: center;
}