.viewcontent-div-main{
    padding: 24px;
    background-image: url('/img/background.png');
    display: flex;
    align-items: center;
    justify-content: center;
}
.viewcontent-div-content {
    width: 100%;
    max-width: 1100px;
    padding: 24px;
    border-radius: 10px;
    background: #ffffff;
    -webkit-box-shadow: 0 0 1em  rgba(150, 150, 150, 0.47);
    -moz-box-shadow:    0 0 1em  rgba(150, 150, 150, 0.47);
    box-shadow:         0 0 1em  rgba(150, 150, 150, 0.47);
}

code {
    font-weight: bold;
    padding: 2px;
    font-family: Monospace;
    font-size: 95%;
}