.about-div-backgroud{
    padding: 24px;
    background-image: url('/img/background.png');
    display: flex;
    align-items: center;
    justify-content: center;
}

.about-div-main {
    width: 100%;
    max-width: 1100px;
    min-height: 300px;
    padding: 24px;    
    border-radius: 10px;
    background:#ffffff;
    -webkit-box-shadow: 0 0 1em  rgba(150, 150, 150, 0.47);
    -moz-box-shadow:    0 0 1em  rgba(150, 150, 150, 0.47);
    box-shadow:         0 0 1em  rgba(150, 150, 150, 0.47);
}

.about-title{
    text-align: center;
    font-size: 32px;
    color: rgb(0,21,41);
}

.about-div-logo{
    padding: 24px;
    display: flex;
    align-items: center;
    justify-content: left;
}

.about-div-logo img{
    width: 150px;
    margin-right: 20px;
}

.about-div-logo p{
    text-align: justify;
}

.about-collaborators{
    display: flex;
    align-items: center;
    justify-content: center;
}

.about-collaborators img{
    min-width: 150px;
    width: 150px;
    height: 150px;
    margin-right: 20px;
    border-radius: 5px;
}

.about-collaborators p{
    text-align: justify;
}
