.signup-div-main {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url('/img/background.png');
}

.signup-icon{
    display: flex;
    align-items: center;
    justify-content: center;
}

.signup-title{
    text-align: center;
    font-size: 40px;
    color: rgb(0,21,41);
}

.signup-form {
    margin-bottom: 50px;
    margin-top: 50px;
    width: 80%;
    max-width:  600px;
    padding: 24px;
    border-radius: 10px;
    background-color: #ffffff;    
    -webkit-box-shadow: 0 0 1em  rgba(150, 150, 150, 0.47);
    -moz-box-shadow:    0 0 1em  rgba(150, 150, 150, 0.47);
    box-shadow:         0 0 1em  rgba(150, 150, 150, 0.47);
}
.signup-form-forgot {
    float: right;
}
.signup-form-button {
    width: 100%;
}

.signup-footer{ 
    width: 100%;
    text-align: center;
  }
  