.category-div-backgroud{
    padding: 24px;
    background-image: url('/img/background.png');
    display: flex;
    align-items: center;
    justify-content: center;
}

.category-div-main {
    width: 100%;
    max-width: 1100px;    
}
.category-div-menu {
    width: 100%;
    display: flex;    
    overflow-x: scroll;
    justify-content: flex-start;
    align-content: flex-start;
    align-items: center;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    background: rgb(0,21,41);
    -webkit-box-shadow: 0 0 1em  rgba(150, 150, 150, 0.47);
    -moz-box-shadow:    0 0 1em  rgba(150, 150, 150, 0.47);
    box-shadow:         0 0 1em  rgba(150, 150, 150, 0.47);
}

.category-div-menu .menu-item {
    display: inline-block;
    text-align: center;
    font-size: 12px;
    color: rgba(255,255,255);
    padding: 10px 10px;
    position: relative;
    opacity: 0.4;
}
.category-div-menu .menu-item:hover{
    opacity: 1;
}
.category-div-menu .menu-img{
    width: 60px;
    padding: 5px;
}
.category-div-content{
    width: 100%;
    height: 100%;
    min-height: 300px;
    padding: 24px;    
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background:#ffffff;
    -webkit-box-shadow: 0 0 1em  rgba(150, 150, 150, 0.47);
    -moz-box-shadow:    0 0 1em  rgba(150, 150, 150, 0.47);
    box-shadow:         0 0 1em  rgba(150, 150, 150, 0.47);
}